import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import mapper from "../mapper";

const Sidebar = ({ isOpen, setIsOpen }) => {
    const [collapsed, setCollapsed] = useState({}); // State to track collapsed/expanded nodes
    const location = useLocation(); // Get current URL from react-router-dom

    // Initialize all nodes as collapsed
    useEffect(() => {
        const initializeCollapsedState = (content, parentKey = "") => {
            const initialState = {};
            Object.keys(content).forEach((key) => {
                if (!key.startsWith("_")) {
                    const currentKey = `${parentKey}/${key}`;
                    initialState[currentKey] = true; // Set all nodes to collapsed
                    if (typeof content[key] === "object") {
                        Object.assign(
                            initialState,
                            initializeCollapsedState(content[key], currentKey)
                        );
                    }
                }
            });
            return initialState;
        };

        setCollapsed(initializeCollapsedState(mapper.Contents));
    }, []); // Run only on component mount

    // Helper function to determine if a path or its ancestors are active
    const isActive = (path) => location.pathname.startsWith(path);
    const isActiveLink = (activeLink) => {
        const lastPath = location.pathname.split("/").pop().replace(/%20/g, " ");
        //console.log(activeLink, lastPath, "Active Link:", activeLink === lastPath);
        return activeLink === lastPath;
    };

    // Toggle collapse for a specific key
    const toggleCollapse = (key) => {
        setCollapsed((prevState) => ({
            ...prevState,
            [key]: !prevState[key], // Toggle the state for the given key
        }));
    };

    // Recursive function to render nested lists with dynamic URLs
    const renderNestedList = (content, basePath = "", parentKey = "") => {
        return (
            <ul className="sidebar-list">
                {Object.keys(content).map((key) => {
                    // Skip keys starting with `_` (like ___urlPath___)
                    if (key.startsWith("_")) return null;

                    const currentKey = `${parentKey}/${key}`; // Unique key for collapsing state
                    const currentPath = `${basePath}/${content[key]?.___urlPath___ || key}`;
                    const isExpanded = !collapsed[currentKey];

                    // If the value is an object, render it recursively
                    if (typeof content[key] === "object") {
                        return (
                            <li
                                key={currentKey}
                                className={`sidebar-item ${isActive(currentPath) ? "active" : ""}`}
                            >
                                <div
                                    className="sidebar-title"
                                    onClick={() => toggleCollapse(currentKey)}
                                >
                                    <span className="sidebar-icon">
                                        {isExpanded ? "" : ""} {/* Arrow */}
                                    </span>
                                    <span>{key}</span>
                                </div>
                                {isExpanded && (
                                    <div className="sidebar-nested">
                                        {renderNestedList(
                                            content[key],
                                            currentPath,
                                            currentKey
                                        )}
                                    </div>
                                )}
                            </li>
                        );
                    } else {
                        // If the value is not an object, render it as a link
                        return (
                            <li
                                key={currentKey}
                                className={`sidebar-item ${isActiveLink(key) ? "linkHighlight" : ""}`}
                            >
                                <Link to={currentPath} className="sidebar-link">
                                    {key}
                                </Link>
                            </li>
                        );
                    }
                })}
            </ul>
        );
    };

    return (
        <div className="sidebar-container">
            <div className="sidebar-header">
                <h2>Contents</h2>
            </div>
            <div className="sidebar-content">
                {renderNestedList(mapper.Contents, `/${mapper.Contents.___urlPath___}`)}
            </div>
        </div>
    );
};

export default Sidebar;
