import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeRaw from "rehype-raw"; // For raw HTML rendering
import rehypeHighlight from "rehype-highlight"; // For syntax highlighting
import "highlight.js/styles/github.css"; // Syntax highlighting theme
import mapper from "../mapper";
import NotFound from "./NotFound";

// Preprocess Markdown to wrap ==highlight== in <mark>
const preprocessMarkdown = (content) => {
    return content.replace(/==(.*?)==/g, "<mark>$1</mark>");
};

const getFileNumberAndKey = (mapperObj, paths) => {
    let current = mapperObj;
    let matchedKey = null;

    for (const path of paths) {
        const decodedPath = decodeURIComponent(path); // Decode the path to handle special characters
        //console.log("Decoded Path:", decodedPath);

        const foundKey = Object.keys(current).find((key) => {
            return (
                (current[key] &&
                    typeof current[key] === "object" &&
                    current[key].___urlPath___ === decodedPath) ||
                key === decodedPath // Direct match for leaf nodes
            );
        });

        if (foundKey) {
            matchedKey = foundKey; // Store the matched key
            current = current[foundKey];
        } else {
            console.error(`Path not found: ${decodedPath}. Current Level:`, current);
            return { key: null, fileNumber: null }; // Exit early if path not found
        }
    }

    return {
        key: matchedKey,
        fileNumber: typeof current === "number" ? current : null,
    };
};


const MarkdownPage = () => {
    const location = useLocation();
    const [markdown, setMarkdown] = useState("");
    const [pageTitle, setPageTitle] = useState("");
    const [error, setError] = useState(false);


    // Extract all path segments dynamically from the URL
    const paths = location.pathname.split("/").filter((segment, index) => index > 1); // Skip "contents"

    useEffect(() => {
        const { key, fileNumber } = getFileNumberAndKey(mapper.Contents, paths); // Get both the title and file number

        setPageTitle(key); // Set the title from the key

        if (fileNumber) {
            const fetchMarkdown = async () => {
                try {
                    const response = await fetch(`/markdowns/${fileNumber}.md`);
                    // Check for valid content type and status
                    const contentType = response.headers.get("Content-Type");
                    if (!response.ok || !contentType || !contentType.includes("text/markdown")) {
                        // it by default returns text/html
                        // expected content type is text/markdown
                        throw new Error("Markdown file not found or invalid response");
                    }
                    const text = await response.text();
                    setMarkdown(preprocessMarkdown(text));

                } catch (error) {
                    console.error("Error loading Markdown file:", error);
                    setError(true);
                }
            };

            fetchMarkdown();
        } else {
            setMarkdown("Invalid topic.");
        }
    }, [paths]);

    if (error) {
        return <NotFound />; // Display the 404 page if there's an error
    }

    return (
        <div style={{ padding: "20px", fontFamily: "Arial, sans-serif", lineHeight: "1.6", marginTop: "20px" }}>
            <h1 style={{ borderBottom: "2px solid #ccc", paddingBottom: "10px" }}>
                {pageTitle || "Untitled"}
            </h1>
            {console.log("Rendered Markdown:", markdown)}
            <ReactMarkdown
                children={markdown}
                remarkPlugins={[remarkGfm]}
                rehypePlugins={[rehypeRaw, rehypeHighlight]} // Enable raw HTML and syntax highlighting
            />
        </div>
    );
};

export default MarkdownPage;
