import React from "react";
import StackGrid from "react-stack-grid";

const Syllabus = () => {

    const categories = [
        {
            title: "Backend - Java",
            items: [
                "Programming in Core Java",
                "Advanced Java",
                "Enterprise Java",
                "Testing",
                "Deployment",
                "Tools",
                "Essential Java for Competitive Programming",
                "Microservices",
                "Additional Concepts",
                "Performance optimisation",
            ],
        },
        {
            title: "Backend - JavaScript",
            items: [
                "NodeJs",
                "ExpressJs",
                "Authentication & Authorisation",
                "Websockets",
            ],
        },
        {
            title: "Security",
            items: ["Web security", "Https & CORS"],
        },
        {
            title: "Computer Science & Applications",
            items: [
                "DS in Java",
                "Algorithms in Java",
                "Databases",
                "System Design",
                "Computer Organization & Architecture",
                "Compiler Design",
                "Software Engineering",
                "Operating Systems",
                "Computer Networks",
            ],
        },
        {
            title: "Frontend",
            items: [
                "Core Concepts",
                "Advanced JavaScript",
                "State Management",
                "API integration",
                "Build tools and environment",
                "Testing and debugging",
                "Performance optimisation UI",
                "UI/UX design principles",
                "Version control and collaboration",
                "Micro-frontend",
            ],
        },
        {
            title: "DevOps",
            items: ["AWS", "CI/CD", "Containerization", "Version Control", "Kubernetes"],
        },
        {
            title: "Behavioral",
            items: ["Softskills"],
        },
        {
            title: "AWS Certified Developer - Associate",
            items: [
                "Getting Started with AWS",
                "AWS Identity & Access Management (AWS IAM)",
                "Amazon EC2  Basics",
                "Amazon EC2  Instance Storage",
                "High Availability & Scalability",
                "RDS, Aurora, & ElastiCache",
                "Amazon Route 53",
                "Amazon VPC  Basics",
                "Amazon S3",
                "AWS CLI, SDK, IAM Roles & Policies",
                "Amazon S3  Advanced",
                "Amazon S3  Security",
                "Amazon CloudFront",
                "Containers on AWS",
                "AWS Elastic Beanstalk",
                "AWS CloudFormation",
                "AWS Integration & Messaging",
                "AWS Monitoring, Troubleshooting & Audit",
                "AWS Lambda",
                "Amazon DynamoDB",
                "Amazon API Gateway",
                "AWS CICD",
                "AWS Serverless Application Model (SAM)",
                "AWS Cloud Development Kit (CDK)",
                "Amazon Cognito",
                "Other Serverless",
                "Advanced Identity in AWS",
                "AWS Security & Encryption",
                "Other Services",
                "Exam Preparation",
                "Congratulations",
            ],
        },
    ];

    return (
        <div style={{ fontFamily: "'Poppins', sans-serif" }}>
            <StackGrid
                columnWidth={300} // Width of each column
                gutterWidth={15} // Horizontal space between tiles
                gutterHeight={15} // Vertical space between tiles
            >
                {categories.map((category, index) => (
                    <div
                        key={index}
                        style={{
                            backgroundColor: "#fff",
                            borderRadius: "10px",
                            boxShadow: "0 5px 15px rgba(0, 0, 0, 0.1)",
                            padding: "20px",
                            transition: "transform 0.3s ease",
                            fontFamily: "'Poppins', sans-serif",
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.transform = "scale(1.03)";
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.transform = "scale(1)";
                        }}
                    >
                        <h2
                            style={{
                                fontSize: "1.5em",
                                color: "#007bff",
                                marginBottom: "10px",
                                borderBottom: "2px solid #ddd",
                                paddingBottom: "5px",
                                textAlign: "center",
                            }}
                        >
                            {category.title}
                        </h2>
                        <ul
                            style={{
                                listStyle: "none",
                                padding: 0,
                                margin: 0,
                                fontSize: "1em",
                                color: "#444",
                                lineHeight: "1.8",
                            }}
                        >
                            {category.items.map((item, idx) => (
                                <li
                                    key={idx}
                                    style={{
                                        marginBottom: "8px",
                                        padding: "5px",
                                        borderRadius: "5px",
                                        backgroundColor: "#f9f9f9",
                                        transition: "background-color 0.3s ease",
                                    }}
                                    onMouseEnter={(e) =>
                                        (e.currentTarget.style.backgroundColor = "#e6f2ff")
                                    }
                                    onMouseLeave={(e) =>
                                        (e.currentTarget.style.backgroundColor = "#f9f9f9")
                                    }
                                >
                                    {item}
                                </li>
                            ))}
                        </ul>
                    </div>
                ))}
            </StackGrid>
        </div>
    );
};

export default Syllabus;
